html,
body {
  overflow:hidden;
  width: 100%;
  height: 100%;
  /* max-width: 1366px; */
  /* max-height: 1024px; */
  margin: auto;
  border: 1px solid var(--primary);
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0,0,0,.5);
    box-shadow: 0 0 1px rgba(255,255,255,.5);
}

#root {
  width: 100%;
  height: 100%;
}

fieldset {
  margin-top: 10px;
  padding: 20px;
  border: 1px dotted black;
  border-radius:5px;
}

legend {
  font-size: 1em;
  background-color: transparent;
}

hr.minimal {
  margin-top: 8px;
  margin-bottom: 8px;
  border-top-width: 2px;
}

.container-fluid,
.main {
  width: 100%;
  margin: 0;
  padding-left: 5px;
  padding-right: 5px;
  height: calc(100% - 58px);
  overflow: hidden;
}

.row {
  height: 100%;
  width: 100%;
  margin-right: 0;
  /* overflow: hidden; */
}

.col {
  height: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  padding: 15px;
  overflow-y: scroll;
  /* overflow-y: hidden; */
  /* -webkit-overflow-scrolling: touch; */
}
.left-nav {
  padding-right: 0;
  padding-bottom: 0;
}
.phase-details {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
}
.left-col {
  padding-left: 10px;
  padding-right: 0;
  overflow-y: auto;
}
.right-col {
  padding-left: 0;
  padding-right: 0;
}

nav.navbar {
  padding-left: 5px;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0;
}
.navbar-text {
  margin-bottom: 0;
}

.nav-tabs {
  width: 100%;
}


ul.nav-tabs a.nav-link {
  padding-left: 6px;
  padding-right: 6px;
  padding-top: 4px;
  padding-bottom: 4px;
}


ul {
  list-style: none;
  padding-left: 20px;
  margin: 0px;
}
ul.first {
  padding-left: 5px;
}

.wrapper {
  width: 70vw;
  height: 85vh;
  margin: auto;
}

.dev {
  border: 1px dashed hotpink;
}

.empty-heading {
  padding-left: 13px;
}

.checkbox-icon {
  transform: translateY(5px);
}
.callout-icon {
  margin-left: 10px;
  transform: translateY(5px);
}
.chevron-icon {
  transform: translateY(5px)
}

.callout {
  margin-left: 10px;
  /* color: blue; */
  font-style: italic;
  overflow: none;
  text-overflow: ellipsis;
}
.callout.currentCallOut {
  background-color: yellow;
}

.card-body {
  padding-top: 5px;
}

.dropdown-menu {
  transform: translateX(-80px);
}
.dropdown-item {
  padding-top: 2px;
  padding-bottom: 2px;
}
.dropdown-header {
  font-weight:900;
  font-size: 1.1rem;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding-top: 2px;
  padding-bottom: 2px;
  background-color: #545b62;
  color: white;
}
.dropdown-depth-1 {
  padding-left: 10px;
}
.dropdown-depth-2 {
  padding-left: 30px;
}

.dropdown-depth-3 {
  padding-left: 50px;
}

.dropdown-form {
  padding-left: 40px;
}


.hidden {
  display: none;
}

li.phase {
  font-size: 1.1em;
  padding-top: 6px;
  padding-bottom: 2px;
  padding-left: 0;
}
li.phase:first-of-type  {
  padding-top: 0;
}

li.sub-phase {
  font-size: 0.9em;
  padding-top: 6px;
  padding-bottom: 2px;
  padding-left: 15px;
}
li.sub-phase:first-of-type {
  padding-top: 0;
  background-color: red;
}

li.menu-depth-1 {
  padding-left: 0;
}
li.menu-depth-2 {
  padding-left: 20px;
}
li.menu-depth-3 {
  padding-left: 40px;

}
li.menu-depth-4 {
  padding-left: 60px;
}

li.menu-heading {
  font-weight: bold;
}

.tab-content {
  height: 100%;
}

.tab-pane {
  overflow-y: auto;
}

.hoverable.active {
  filter: invert(57%) sepia(92%) saturate(4739%) hue-rotate(342deg) brightness(108%) contrast(101%);
}

.hoverable:hover {
  filter: invert(78%) sepia(44%) saturate(1107%) hue-rotate(164deg) brightness(102%) contrast(97%);
}

.stick-figure-nav {
  display: flex;
  flex-flow: row;
}

.vert-intv-menu {
  padding-right: 0;
  padding-left: 0;
}

.vert-tabs-col {
  padding-left: 0;
  padding-right: 0;
}

.vert-tabs .nav-link {
  padding-top: 6px;
  padding-bottom: 6px;
}